.Pokecard {
  width: 300px;
  padding: 10px;
  margin: 1rem;
  box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
  text-align: center;
  background-color: #fff;
  border-radius: 3px;
}

.Pokecard-data {
  font-size: 80%;
}

.Pokecard img:hover {
  transform: scale(1.25);
  transition: 1s;
  opacity: 0.8;
}